import React, { useEffect, useState } from 'react';
import './Scss/Infografics.scss';
import Slider from 'react-slick';
import CountUp from 'react-countup';
import { useTranslation } from 'react-i18next';
import Img from '../../assets/images/infografic.png';

const Infografics = ({ dark, english, russian, uzbek, mandarin }) => {

    const [number1, setNumber1] = useState(0);
    const [number2, setNumber2] = useState(0);
    const [number3, setNumber3] = useState(0);
    const [number4, setNumber4] = useState(0);

    const observer = new IntersectionObserver(
        entries => {
            entries.forEach(entry => {
                window.addEventListener("scroll", () => {
                    if (window.scrollY - entry.target.offsetTop > -window.innerHeight) {
                        setNumber1(1000)
                        setNumber2(300)
                        setNumber3(600)
                        setNumber4(150)
                    } else if (window.scrollY - entry.target.offsetTop < 0) {
                        setNumber1(0)
                        setNumber2(0)
                        setNumber3(0)
                        setNumber4(0)
                    }
                })
            })
        }
    );

    useEffect(() => {
        const infografics = document.getElementById("infografics");
        observer.observe(infografics);
    }, []);

    let dataNumbers = [
        { id: 0, number: number1, text_uz: "Yillik loyihalar soni", text_ru: "Количество проектов в год", text_en: "Number of projects per year", text_mn: "每年的項目數量", description_uz: "Biz yiliga keltirilgan raqamlardagi loyihalarni bajaramiz. Bularning ichida Temir yo’l orqali yetkazib berishlar, Havo yo’li orqali yetkazib berishlar, Quruqlik orqali yetkazib berishlarni amalga oshiramiz. Siz ham bizning mijozimiz bo’lishingiz uchun bizga qo’ng’iroq qiling va sevimli mijozlarimizdan biriga aylaning", description_ru: "Мы выполняем проекты в количестве, указанном в год. Среди них мы осуществляем доставку по железной дороге, доставку по воздуху и доставку по суше. Позвоните нам и станьте одним из наших любимых клиентов, чтобы стать нашим клиентом", description_en: "We carry out projects in the numbers indicated per year. Among them, we carry out deliveries by railway, deliveries by air, and deliveries by land. Call us and become one of our favorite customers to become our customer", description_mn: "我們每年執行指定數量的項目。 其中，我們進行鐵路發貨，空運發貨，陸運發貨。 打電話給我們，成為我們最喜歡的客戶之一成為我們的客戶" },
        { id: 1, number: number2, text_uz: "Ortiq davlatlarga chiqish", text_ru: "Доступ к большему количеству стран", text_en: "Access to more countries", text_mn: "進入更多國家", description_uz: "Biz qariyb barcha dunyo mamlakatlari bilan hamkorlik asosida ishlaymiz va sizning yukingizni istalgan joydan olib kelamiz yoki yetkazib beramiz", description_ru: "Мы работаем в сотрудничестве практически со всеми странами мира и можем забрать или доставить ваш груз из любой точки мира.", description_en: "We work in cooperation with almost all countries of the world and we can pick up or deliver your cargo from anywhere", description_mn: "我們與世界上幾乎所有國家都有合作，我們可以從任何地方提取或交付您的貨物" },
        { id: 2, number: number3, text_uz: "Minnatdor mijozlar", text_ru: "Благодарные клиенты", text_en: "Grateful customers", text_mn: "感恩客戶", description_uz: "Bizning xizmatimizdan foydalangan mijozlarimiz soni bundanda ortiq. Siz ham ularning biriga aylaning", description_ru: "Количество клиентов, воспользовавшихся нашими услугами, намного больше. Стань одним из них", description_en: "The number of clients who have used our service is more than that. Become one of them", description_mn: "使用我們服務的客戶數量不止於此。 成為其中一員" },
        { id: 3, number: number4, text_uz: "Xodimlarimiz soni", text_ru: "Количество наших сотрудников", text_en: "Number of our employees", text_mn: "我們的員工人數", description_uz: "Bizda o’z sohasaning mutaxassislaridan tashkil topgan jamoa bo’lib sizning yukingizni tez va xavfsiz yetkazib berishga kafolat beramiz", description_ru: "У нас есть команда профессионалов своего дела, и мы гарантируем быструю и безопасную доставку вашего груза", description_en: "We have a team of experts in our field, and we guarantee fast and safe delivery of your cargo", description_mn: "我們擁有該領域的專家團隊，我們保證快速安全地交付您的貨物" }
    ];

    const [activeId, setActiveId] = useState(0);

    const settings = {
        dots: true,
        infinite: true,
        autoplay: true,
        slidesToShow: 1,
        speed: 2000,
        pauseOnHover: false,
        slidesToScroll: 1,
        autoplaySpeed: 5000,
        cssEase: "linear",
        beforeChange: (current, next) => setActiveId(next),
    };

    // i18next

    const { t } = useTranslation();

    return (
        <div className={`Infografics main ${!dark && "light-infografics"}`} id='infografics'>
            <div className="title">{t("infographic")}</div>
            <div className="wrapper">
                <img src={Img} alt="truck" className='side-truck' />
                <div className="rectangle"></div>
                <div className="numbers">
                    {dataNumbers.map((data) => (
                        <div key={data.id} className="number col-3">
                            <div className="body" onClick={() => setActiveId(data.id)}>
                                <h1 className={`count ${activeId == data.id && "active"}`}>
                                    <CountUp start={0} end={data.number} duration={4} />
                                </h1>
                                <p className={`text ${data.id != activeId && "d-none"}`}>{uzbek && data.text_uz}{russian && data.text_ru}{english && data.text_en}{mandarin && data.text_mn}</p>
                            </div>
                        </div>
                    ))}
                </div>
                <Slider {...settings} className='carousel'>
                    {dataNumbers.map((data) => (
                        <p key={data.id} className="desc">{uzbek && data.description_uz}{russian && data.description_ru}{english && data.description_en}{mandarin && data.description_mn}</p>
                    ))}
                </Slider>
            </div>
        </div>
    );
};

export default Infografics;